//  vuex .
import Store from "../store/index";

// 
// :
// /router/index.js
// /components/MerchandiseItem.vue
// /components/RecipientList.vue
// /views/ShoppingCart.vue

// .
let timer = null;

function loaded() {
  // .
  clearTimeout(timer);
  // .
  Store.state.isLoading = false;
}

export default {
  // .
  isLoading() {
    // .
    Store.state.isLoading = true;

    // .
    clearTimeout(timer);
    //  500 .
    timer = setTimeout(loaded, Store.state.loadingTime);
  },

  // .
  isLoaded() {
    // .
    clearTimeout(timer);
    // .
    Store.state.isLoading = false;
  },
};
