/**
 *  - 
 * , 
 * @param {function} func - event function
 * @param {number} wait - , , ,
 * 
 * @param {number} limit - , 
 * @param {boolean} immediate - true , 
 * , false 
 * ,  resize() wheel() mousemove()
 * keydown() ...
 */
export default function(
  func,
  { wait = 1000 / 30, limit = 0, immediate = false }
) {
  // target -  this 
  // event -  event 
  // startTime - 
  // currentTime - 
  // timer -  setTimeout,  clear setTimeout
  let target, event, startTime, currentTime, timer;
  // 
  function waitFunc() {
    func.call(target, event);
    startTime = currentTime;
  }
  // 
  return function(e) {
    // target -  this 
    target = this;
    // event -  event 
    event = e;
    // , ,  1970-01-01 00:00:00 UTC 
    currentTime = new Date().getTime();
    /**
     * 
     * immediate 
     * startTime  undefined, startTime = currentTime;
     */
    if (immediate && startTime === undefined) {
      waitFunc();
    }
    // 
    clearTimeout(timer);
    //  && 
    if (limit && currentTime - startTime > limit) {
      waitFunc();
    } else {
      // , , wait 
      timer = setTimeout(waitFunc, wait);
    }
  };
}
