<template>
  <div class="categories">
    <Header />

    <!--  -->
    <Carousel :items="$store.state.slides" />

    <!--  -->
    <div class="breadcrumb-container">
      <Breadcrumb
        :homeLink="'/home'"
        :category="$route.params.category"
        :categoryName="categoryObject.name"
        :categoryLink="getCategoryLink(categoryTitle)"
      />
    </div>

    <!-- RWD . -->
    <div class="screen-container">
      <!-- . -->
      <div class="categories-container">
        <div class="items">
          <!-- ,  RWD. -->
          <div
            class="item-container"
            v-for="(value, key) of calcData"
            :key="key"
          >
            <!-- . -->
            <CategoryItem
              :src="value.imageSrc"
              :alt="value.alt"
              :name="value.name"
              :categoryLink="getCategoryLink(key)"
              :isActive="$route.params.category === key"
            />
          </div>
        </div>
      </div>

      <!--  -->
      <div class="merchandises-container">
        <p class="title">{{ categoryObject.name }}</p>

        <!-- ,  RWD. -->
        <div class="items">
          <div
            class="item-container"
            v-for="(item, i) of showMerchandises"
            :key="i"
          >
            <!-- . -->
            <MerchandiseItem
              :id="item.id"
              :category="item.category"
              :categoryName="item.categoryName"
              :name="item.name"
              :text="item.text"
              :imageSrc="item.imageSrc"
              :alt="item.alt"
              :remaining="item.remaining"
              :originalPrice="item.originalPrice"
              :specialPrice="item.specialPrice"
              :merchandiseLink="
                getMerchandiseLink({ category: item.category, id: item.id })
              "
            />
          </div>
        </div>

        <!-- . -->
        <div class="pagination-container" v-show="pages">
          <Pagination
            :index="index"
            :length="length"
            :pages="pages"
            @setIndex="setIndexHandler"
          />
        </div>
      </div>
    </div>

    <Footer />
  </div>
</template>

<script>
// .

import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";

// .
import Carousel from "../components/Carousel.vue";
// .
import Breadcrumb from "../components/Breadcrumb.vue";
// .
import CategoryItem from "../components/CategoryItem.vue";
// .
import MerchandiseItem from "../components/MerchandiseItem.vue";
// .
import Pagination from "../components/Pagination.vue";
//  route .
import checkRoute from "../modules/checkRoute";

export default {
  name: "Categories",

  data() {
    return {
      // .
      index: 0,
      // .
      length: 5,
    };
  },

  computed: {
    // data, .
    calcData() {
      return this.$store.getters.calcData;
    },

    // , .
    categoryTitle() {
      return this.$route.params.category;
    },

    // .
    categoryObject() {
      return this.calcData[this.categoryTitle];
    },

    // .
    merchandises() {
      return this.categoryObject.merchandises;
    },

    // .
    showMerchandises() {
      // .
      const start = this.index * this.length;
      //  end element.
      const end = start + this.length;

      return this.merchandises.length < this.length
        ? this.merchandises
        : this.merchandises.slice(start, end);
    },

    // .
    pages() {
      return this.merchandises.length > this.length
        ? Math.ceil(this.merchandises.length / this.length)
        : 0;
    },
  },

  methods: {
    //  link.
    getCategoryLink(category) {
      return "/categories/" + category;
    },

    //  link.
    getMerchandiseLink({ category, id }) {
      return "/merchandise/" + category + "/" + id;
    },

    // .
    setIndexHandler(i) {
      this.index = i;
      this.index =
        this.index < 0
          ? 0
          : this.index > this.getPage
          ? this.getPage
          : this.index;
    },
  },

  //  route .
  beforeRouteUpdate(to, from, next) {
    //  index .
    this.index = 0;

    //  route category .
    checkRoute.category(to.params.category) ? next() : next("/home");
  },

  components: {
    Header,
    Footer,
    Carousel,
    Breadcrumb,
    CategoryItem,
    MerchandiseItem,
    Pagination,
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";
@import "../assets/style/mixin.scss";
@import "../assets/style/class.scss";

.categories {
  //  path.
  .breadcrumb-container {
    padding: 0.375rem 0 0.375rem 0.625rem;
  }

  // .
  .categories-container {
    padding: 0.375rem;

    .items {
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-between;
      align-items: center;

      .item-container {
        flex-basis: 18%;
      }
    }
  }

  // .
  .merchandises-container {
    padding: 0 0.375rem;

    .title {
      padding: 0.375rem 0;
      @include font-style(
        $font-size: 1.5rem,
        $font-weight: 900,
        $color: $green
      );
    }

    .item-container {
      padding-bottom: 0.625rem;

      &:last-child {
        padding-bottom: 0px;
      }
    }
  }

  // .
  .pagination-container {
    padding: 0.625rem 0;
  }
}

@media only screen and (min-width: $screen-width-md) {
  .categories {
    .screen-container {
      width: 100%;
      // .
      @include clearfix();

      .categories-container {
        float: left;
        width: 20%;
        box-sizing: border-box;
        padding: 0px 0.625rem 0 0;

        .items {
          flex-wrap: wrap;

          .item-container {
            flex-basis: 100%;
            margin-bottom: 1rem;

            &:last-child {
              margin-bottom: 0px;
            }
          }
        }
      }

      .merchandises-container {
        float: left;
        width: 80%;
        padding: 0px;
        box-sizing: border-box;

        .title {
          padding: 0px 0px 0.625rem 0;
          box-sizing: border-box;
        }

        .items {
          .item-container {
            padding-right: 0.625rem;
            width: calc((100% - 1.25rem) / 3);
            display: inline-block;

            &:nth-of-type(3n) {
              padding-right: 0px;
            }
          }
        }
      }
    }
  }
}
</style>
