import Vue from "vue";
// vee-validate, .
// ValidationProvider  input .
// ValidationObserver  from .
import { ValidationProvider, ValidationObserver } from "vee-validate";

// .
Vue.component("ValidationProvider", ValidationProvider);
Vue.component("ValidationObserver", ValidationObserver);

//  vee-validate .
import { extend } from "vee-validate";
// .
// required .
// email .
// alpha_num .
// between .
// digits , .
// length ,  value .
import { required, email, length, alpha_num } from "vee-validate/dist/rules";

// .
extend("required", {
  ...required,
  message: ".",
});

// .
extend("email", {
  ...email,
  message: " Email .",
});

// .
extend("notNumber", {
  validate: (value) => {
    return !value.match(/[0-9]/);
  },
  message: ".",
});

// .
extend("length", {
  ...length,
  message: ".",
});

extend("phone", {
  validate: (value) => {
    return value.match(/^09/);
  },
  message: " 09 .",
});

// .
extend("address", {
  validate: (value) => {
    return !value.match(/[a-zA-Z]+/);
  },
  message: ".",
});

// .
extend("alpha_num", {
  ...alpha_num,
  message: ".",
});
